import { Component, OnInit, ViewChild } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { Observable, from } from 'rxjs';
import { ImageCroppedEvent, ImageCropperComponent, ImageTransform } from 'ngx-image-cropper';
import { Crop } from '@ionic-native/crop/ngx';
import { AngularFireStorage } from '@angular/fire/storage';
import { LoadingController, AlertController, ToastController } from '@ionic/angular';
// Pass data between pages
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router'
import { AngularFirestore } from '@angular/fire/firestore';

@Component({
  selector: 'app-tab3',
  templateUrl: 'tab3.page.html',
  styleUrls: ['tab3.page.scss']
})
export class Tab3Page {


  constructor(public loadingController: LoadingController,
    public alertController: AlertController,
    public afSG: AngularFireStorage,
    public afAuth: AngularFireAuth,
    private Afs: AngularFirestore,
    private camera: Camera,
    private crop: Crop,
    public router: Router,
    private route: ActivatedRoute,
    public toastController: ToastController) {
    this.recepData();


  }
  calendrier = {
    date: new Date(),
    heureD: '',
    heureF: ''
  };

  userId: string;

  dataEvent = {
    titre: '.d',
    description: '.',
    deadline: '12',
    geo: '.',
    maxgens:'.',
    photocrop:'.'
  }

  recepData() {
    // receptionner dataUser
    this.route.queryParams.subscribe(params => {
      console.log('params:', params);
      if (this.router.getCurrentNavigation().extras.state) {
        this.calendrier = this.router.getCurrentNavigation().extras.state.dataUser;

        console.log("recep" + this.calendrier.date);



      }
    })
    this.getAuth();
  }

  getAuth() {

    //recup info auth
    this.afAuth.authState.subscribe(auth => {
      this.userId = auth.uid;
      console.log("userID:   " + this.userId)

    });
  }

  Post() {
    this.uploadFirestore();
  }

  uploadFirestore() {
    this.presentToast(this.userId)
    this.Afs.collection("privateassos").doc(this.userId).set({
      date: this.calendrier.date,

    }, { merge: true })
      .then(function () {
        console.log("Document successfully written!");
      })
      .catch(function (error) {
        console.error("Error writing document: ", error);
      });
  }

  async presentToast(message: string) {
    const toast = await this.toastController.create({
      duration: 2000,
      message
    });
    toast.present();
  }

}
